import React from 'react';
import {Form, Icon, Input, Button, message} from 'antd';
import $ from 'jquery';

class Login extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            ...this.initState()
        }
    }

    initState = () => {
        return {
            expression: '',
            validate: '',
            validateInput: ''
        };
    };

    renderCode = () => {
        //expressionresultexpressionresult
        let expression = '', result;

        result = 0;//result0
        //
        let Calpre = Math.round(Math.random() * 100);
        let Calafter = Math.round(Math.random() * 100);

        let codeCal = ['-', '+', 'x'];//
        let i = Math.round(Math.random() * 2);//

        switch (codeCal[i]) {//
            case '-':
                expression = Calpre + '-' + Calafter;
                result = Calpre - Calafter;
                break;
            case '+':
                expression = Calpre + '+' + Calafter;
                result = Calpre + Calafter;
                break;
            case 'x':
                expression = Calpre + 'x' + Calafter;
                result = Calpre * Calafter;
                break;
            default:
                break;
        }

        this.setState({//
            expression: expression,
            validate: result
        });
    };

    validate = (rule, value, callback) => {
        let thisInput = value;
        let validateCode = this.state.validate;
        if (thisInput === undefined || thisInput === '') {
            callback('!');
        }
        if (thisInput !== '' && thisInput !== undefined && thisInput.toLowerCase() !== validateCode.toString().toLowerCase()) {
            callback('!');
        }
    };

    handleSubmit = (e) => {
        e.preventDefault();
        let name = this.props.form.getFieldValue('userName');
        let password = this.props.form.getFieldValue('password');
        let captcha = this.props.form.getFieldValue('captcha');
        if (name !== undefined && password !== undefined && captcha!== undefined){
            $.post("/bookstoreApp/checkuser", {name: name, password: password}, function (data) {
                if (JSON.parse(data)[0]) {
                    if (JSON.parse(data)[1]) {
                        message.info(',!');
                    }
                    else {
                        if (JSON.parse(data)[2]) {
                            message.info('!');
                        }
                        else {
                            message.info('!');
                        }
                    }
                }
                else {
                    message.info('!');
                }
            });
        }
        this.props.form.validateFields((err, values) => {});
    };

    checkUserName = (rule, value, callback) => {
        let reg = /^[A-Za-z]+$/;
        if (value && !reg.test(value[0])) {
            callback('!')
        }
        callback()
    };

    render() {
        const {getFieldDecorator} = this.props.form;
        return (
            <Form onSubmit={this.handleSubmit} className="login-form">
                <Form.Item>
                    {getFieldDecorator('userName', {
                        rules: [{required: true, message: '!'}, {
                            validator: this.checkUserName
                        }],
                    })(
                        <Input prefix={<Icon type="user" style={{color: 'rgba(0,0,0,.25)'}}/>} placeholder=""/>
                    )}
                </Form.Item>
                <Form.Item>
                    {getFieldDecorator('password', {
                        rules: [{required: true, message: '!'}],
                    })(
                        <Input prefix={<Icon type="lock" style={{color: 'rgba(0,0,0,.25)'}}/>} type="password"
                               placeholder=""/>
                    )}
                </Form.Item>
                <Form.Item>
                    {getFieldDecorator('captcha', {
                        rules: [{required: true, message: '!'}, {
                            validator: this.validate
                        }],
                    })(
                        <Input  prefix={<Icon type="qrcode" style={{color: 'rgba(0,0,0,.25)'}}/>} className="login-form-input-captcha" placeholder="" />
                    )}
                    <Button type="primary"
                            onClick={this.renderCode}
                            className="login-form-button-captcha">
                        {this.state.expression}</Button>
                </Form.Item>
                <Form.Item>
                    <Button type="primary" htmlType="submit" className="login-form-button">
                        
                    </Button>
                    <a href="">?</a>
                    <a className="login-form-register"
                       onClick={() => this.props.history.push('/registration')}>!</a>
                </Form.Item>
            </Form>
        );
    }
}

const LoginForm = Form.create()(Login);

export default LoginForm;


